<script setup lang="ts">
    import { type ControlSize, type ISelectOption, GpnSelect, GpnButton } from "gpn-ui";
    import { computed, ref } from "vue";
    import { useGlobalStore } from "../stores/store";

    const store = useGlobalStore();
    const selItem = ref(null);
    const selItemMul = ref([{ id: "2", name: "Option 2" }]);
    const selItemSearch = ref(null);
    const selItemMultiSearch = ref([]);
    const selItemSearchLoading = ref(null);
    const isLoading = ref(false);
    const selItemValueOut = ref([]);

    const localSearchText = ref("");
    const localSearchTextMulti = ref("");
    const localSearchTextLoading = ref("");

    const options = ref<ISelectOption[]>([
        { id: "0", name: "Group 1", is_group: true },
        { id: "10", name: "Group 2", is_group: true },
        { id: "1", name: "Option 1", group_id: "0" },
        { id: "2", name: "Option 2" },
        { id: "3", name: "Option 3" },
        { id: "4", name: "Option 4 Option 4 Option 4Option 4Option 4 Option 4 Option 4Option 4 Option 4 Option 4Option 4" },
        { id: "5", name: "Option 5" },
        { id: "6", name: "Option 6", group_id: "10" },
        { id: "7", name: "Option 7", group_id: "10" },
        { id: "8", name: "Option 8" },
        { id: "9", name: "Option 9" },
    ]);

    const sizes = ref<ISelectOption[]>([
        { id: "xl", name: "XL" },
        { id: "l", name: "L" },
        { id: "m", name: "M" },
        { id: "s", name: "S" },
        { id: "xs", name: "XS" },
    ]);

    const selSize = ref<ISelectOption>({ id: "m", name: "M" });

    const localSearchList = computed(() => {
        if (localSearchText.value == "") return options.value;
        else {
            return options.value.filter((f) => !f.is_group && f.name.toLocaleLowerCase().indexOf(localSearchText.value.toLocaleLowerCase()) >= 0);
        }
    });
    const localSearchMultiList = computed(() => {
        if (localSearchTextMulti.value == "") return options.value;
        else {
            return options.value.filter((f) => !f.is_group && f.name.toLocaleLowerCase().indexOf(localSearchTextMulti.value.toLocaleLowerCase()) >= 0);
        }
    });
    const localSearchListLoading = computed(() => {
        if (localSearchTextLoading.value == "") return options.value;
        else {
            return options.value.filter((f) => !f.is_group && f.name.toLocaleLowerCase().indexOf(localSearchTextLoading.value.toLocaleLowerCase()) >= 0);
        }
    });

    function onLocalSearch(text: string) {
        localSearchText.value = text;
    }
    function onLocalSearchMulti(text: string) {
        localSearchTextMulti.value = text;
    }
    function onLocalSearchLoading(text: string) {
        isLoading.value = true;
        setTimeout(() => {
            localSearchTextLoading.value = text;
            isLoading.value = false;
        }, 1000);
    }
</script>
<template>
    <h2>Select</h2>
    <div class="grid">
        <h5>View - default</h5>
        <div>XL</div>
        <div>L</div>
        <div>M</div>
        <div>S</div>
        <div>XS</div>

        <h5>Single</h5>
        <div><GpnSelect v-model="selItem" size="xl" clearable :options="options" label="Select one item" caption="caption text" placeholder="Select one item..." :theme="store.theme" /></div>
        <div><GpnSelect v-model="selItem" size="l" clearable :options="options" label="Select one item" caption="caption text" placeholder="Select one item..." :theme="store.theme" /></div>
        <div style="display: flex; gap: 4px">
            <GpnSelect v-model="selItem" size="m" clearable :options="options" placeholder="Select one item..." :theme="store.theme" />
            <GpnButton label="Click me" />
        </div>
        <div><GpnSelect v-model="selItem" size="s" clearable :options="options" disabled label="Select one item" caption="caption text" placeholder="Select one item..." :theme="store.theme" /></div>
        <div><GpnSelect v-model="selItem" size="xs" clearable :options="options" label="Select one item" caption="caption text" placeholder="Select one item..." :theme="store.theme" /></div>

        <h5>Multiple</h5>
        <GpnSelect v-model="selItemMul" size="xl" multiple :options="options" label="Select many item" caption="caption text" placeholder="Select many item..." :theme="store.theme" />
        <GpnSelect v-model="selItemMul" size="l" multiple :options="options" label="Select many item" caption="caption text" placeholder="Select many item..." :theme="store.theme" />
        <GpnSelect v-model="selItemMul" size="m" multiple :options="options" label="Select many item" caption="caption text" placeholder="Select many item..." :theme="store.theme" />
        <GpnSelect v-model="selItemMul" size="s" multiple :options="options" label="Select many item" caption="caption text" placeholder="Select many item..." :theme="store.theme" />
        <GpnSelect v-model="selItemMul" size="xs" multiple :options="options" label="Select many item" caption="caption text" placeholder="Select many item..." :theme="store.theme" />

        <h5>Single search</h5>
        <GpnSelect
            v-model="selItemSearch"
            search
            size="m"
            clearable
            :options="localSearchList"
            label="Select one item"
            caption="caption text"
            placeholder="Select one item..."
            :theme="store.theme"
            @search="onLocalSearch"
        />
        <GpnSelect
            v-model="selItemMultiSearch"
            search
            multiple
            size="m"
            clearable
            :options="localSearchMultiList"
            label="Select many items"
            caption="caption text"
            placeholder="Select some items..."
            :theme="store.theme"
            @search="onLocalSearchMulti"
        />
        <GpnSelect
            v-model="selItemSearchLoading"
            search
            size="m"
            clearable
            :options="localSearchListLoading"
            :max-height="100"
            label="Select one item"
            caption="loading state & max-height"
            placeholder="Select one item..."
            :loading="isLoading"
            :theme="store.theme"
            @search="onLocalSearchLoading"
        />
        <div style="display: flex">
            <GpnSelect
                v-model="selItemSearchLoading"
                search
                form="clear-clear"
                size="m"
                clearable
                :options="localSearchListLoading"
                :max-height="100"
                label="Select one item"
                caption="loading state & max-height"
                placeholder="Select one item..."
                :loading="isLoading"
                :theme="store.theme"
                @search="onLocalSearchLoading"
            />
            <GpnButton form="brick-default" label="Ok" style="margin-top: 19px" />
        </div>
        <div></div>
    </div>
    <h5>Value out</h5>
    <div style="display: flex; gap: 20px; margin-bottom: 20px">
        <div style="width: 300px">
            <GpnSelect
                v-model="selItemValueOut"
                value-out
                bold
                multiple
                :size="(String(selSize?.id) || 'm') as ControlSize"
                clearable
                :options="localSearchMultiList"
                label="Select many items"
                caption="caption text"
                placeholder="Select some items..."
                :theme="store.theme"
            />
        </div>
        <div style="width: 90px">
            <GpnSelect v-model="selSize" bold :options="sizes" label="Select size" :theme="store.theme" />
        </div>
    </div>
    <div style="width: 300px">
        <GpnSelect
            v-model="selItemValueOut"
            value-out
            out-type="row"
            multiple
            size="m"
            clearable
            :options="localSearchMultiList"
            label="Select many items"
            caption="caption text"
            :theme="store.theme"
            placeholder="Select some items..."
        />
    </div>
</template>

<style lang="scss">
    .grid {
        display: grid;
        grid-template-columns: 90px repeat(5, minmax(0, 1fr)) 10px;
        grid-template-rows: repeat(6, 1fr);
        grid-auto-flow: column;
        gap: 16px;
        align-items: center;
        margin-bottom: 48px;
    }
    h5 {
        color: #2f5e7b;
        font-size: 12px;
    }
</style>
